.loader {
  box-sizing: border-box;
  background: linear-gradient(#242424 30px, #0000 0) 50% 0 / 2px 40px no-repeat;
  border-top: 5px solid #242424;
  border-bottom: 5px solid #242424;
  width: 50px;
  height: 80px;
  animation: 2s linear infinite spinx;
  display: inline-block;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:before, .loader:after {
  content: "";
  background: #fff6 0 0 / 100% no-repeat;
  border: 1px solid #242424;
  border-radius: 0 0 20px 20px;
  width: 40px;
  height: 35px;
  animation: 2s linear infinite lqt;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.loader:after {
  border: 1px solid #242424;
  border-radius: 20px 20px 0 0;
  animation: 2s linear infinite lqb;
  top: auto;
  bottom: 0;
}

@keyframes lqt {
  0%, 100% {
    background-image: linear-gradient(#242424 40px, #0000 0);
    background-position: 0 0;
  }

  50% {
    background-image: linear-gradient(#242424 40px, #0000 0);
    background-position: 0 40px;
  }

  50.1% {
    background-image: linear-gradient(#242424 40px, #0000 0);
    background-position: 0 -40px;
  }
}

@keyframes lqb {
  0% {
    background-image: linear-gradient(#242424 40px, #0000 0);
    background-position: 0 40px;
  }

  100% {
    background-image: linear-gradient(#242424 40px, #0000 0);
    background-position: 0 -40px;
  }
}

@keyframes spinx {
  0%, 49% {
    background-position: 50% 36px;
    transform: rotate(0);
  }

  51%, 98% {
    background-position: 50% 4px;
    transform: rotate(180deg);
  }

  100% {
    background-position: 50% 36px;
    transform: rotate(360deg);
  }
}

